const correctPassword = false;

function checkPassword() {
  document.addEventListener(
    "DOMContentLoaded",
    () => {
      const password = "crumpetgang";

      const question = prompt("Password?");

      if (question === password) {
        correctPassword = true;
      } else {
        checkPassword();
      }
    },
    { once: true }
  );
}
checkPassword();

function isPassGood() {
  if (!correctPassword) {
    checkPassword();
  }
}
